code {
  background-color: #ddd;
  color: #444;
  padding: 0 4px;
  font-family: monospace;
}

pre {
  padding: 8px;
  margin: 1rem 0;
  text-align: left;
  overflow-x: auto;
  background-color: #ddd;

  & code {
    padding: 0;
    background-color: transparent;
    font-size: 1em;

    .hljs-comment {
      color: #777;
    }
  }
}
